<h2 id="zhi-ding-ji-huo-fang-shi"><a class="header-anchor" href="#zhi-ding-ji-huo-fang-shi" aria-hidden="true"></a> </h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;div class=\&quot;demo-poptip\&quot;&gt;\n    &lt;xl-poptip content=\&quot;\&quot; trigger=\&quot;click\&quot;&gt;\n        &lt;xl-button&gt;&lt;/xl-button&gt;\n    &lt;/xl-poptip&gt;\n&lt;/div&gt;\n&lt;div class=\&quot;demo-poptip\&quot;&gt;\n    &lt;xl-poptip content=\&quot;\&quot; trigger=\&quot;hover\&quot;&gt;\n        &lt;xl-button&gt;&lt;/xl-button&gt;\n    &lt;/xl-poptip&gt;\n&lt;/div&gt;\n&lt;xl-poptip content=\&quot;\&quot; v-model=\&quot;visiable\&quot; trigger=\&quot;custom\&quot;&gt;\n    &lt;xl-checkbox v-model=\&quot;visiable\&quot;&gt;&lt;/xl-checkbox&gt;\n&lt;/xl-poptip&gt;\n\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default{\n        data(){\n            return{\n                visiable:false\n            }\n        }\n    }\n&quot;,&quot;style&quot;:&quot;\n    .demo-poptip{\n        margin-bottom:2em;\n    }\n&quot;}">
                            <div><div class="demo-poptip">
