    <span class="hljs-tag">&lt;<span class="hljs-name">xl-poptip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">""</span> <span class="hljs-attr">trigger</span>=<span class="hljs-string">"click"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">xl-poptip</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-poptip"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-poptip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">""</span> <span class="hljs-attr">trigger</span>=<span class="hljs-string">"hover"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">xl-poptip</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">xl-poptip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">""</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"visiable"</span> <span class="hljs-attr">trigger</span>=<span class="hljs-string">"custom"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"visiable"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-poptip</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
        data(){
            <span class="hljs-keyword">return</span>{
                <span class="hljs-attr">visiable</span>:<span class="hljs-literal">false</span>
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
    <span class="hljs-selector-class">.demo-poptip</span>{
