    <span class="hljs-tag">&lt;<span class="hljs-name">xl-poptip</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"content"</span> <span class="hljs-attr">padding</span>=<span class="hljs-string">"0"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span>2<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">xl-poptip</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"content"</span> <span class="hljs-attr">padding</span>=<span class="hljs-string">"0"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span>&gt;</span>3<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"content"</span>&gt;</span>3<span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">xl-poptip</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">xl-poptip</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-poptip</span>&gt;</span>

</code></pre>
</div></demo-block>
<h2 id="poptip-shu-xing"><a class="header-anchor" href="#poptip-shu-xing" aria-hidden="true"></a> Poptip </h2>
<table class="table"><thead>
<tr>
<th></th>
<th></th>
<th></th>
<th></th>
<th></th>
</tr>
</thead>
