<td>-</td>
<td>false</td>
</tr>
<tr>
<td>title</td>
<td></td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>content</td>
<td></td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>padding</td>
<td></td>
<td>string</td>
<td>-</td>
<td>'0.5em,1em'</td>
</tr>
<tr>
<td>options</td>
<td>popper.js<a href="https://popper.js.org/popper-documentation.html">popper.js</a></td>
<td>string</td>
<td>-</td>
<td>'0.5em,1em'</td>
</tr>
<tr>
<td>pop-class</td>
<td></td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>transition-name</td>
<td></td>
<td>string</td>
<td>-</td>
<td>xl-poptip-fade</td>
</tr>
<tr>
<td>dangerous-html</td>
<td>HTML</td>
<td>boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>arrow</td>
<td></td>
<td>boolean</td>
<td>-</td>
<td>true</td>
</tr>
